import {
	getInvite,
} from '@/api/job'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
export default {
	data() {
		return {
			detail: {
				invite: 1573524563
			},
		}
	},
	onLoad(e) {
		this.getInvite(e.id)
	},
	methods: {
		getInvite(id) {
			getInvite({
				id
			}).then(response => {
				let r = response.data
				if (response.code === 20000 && r.detail) {
					this.detail = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
					setTimeout(() => {
						uni.navigateBack();
					}, 1500)
				}
			})
		},
		Back() {
			uni.navigateBack();
		}
	}
}
